.cont {
  position: relative;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 2vw;
  margin: 0 0 47px 0;
}

.swiper-wrapper.gallery {
  align-items: center;
}

.swiper-pagination {
  margin: 30px 0;
  position: relative;
}

.th {
  height: auto !important;
}

.gt {
  height: auto !important;
  width: 100% !important;
  /* transform: initial !important; */
}

.description {
  order: 2;
  display: grid;
  grid-gap: 5vw;
  grid-template-columns: 1fr 1fr;
  max-width: 600px;
  margin: 0 auto 0rem;
  grid-template-rows: min-content;
}

.description .item_main {
  padding: 0.25rem 1rem 0 0;
}

.title_main {
  display: block;
  color: #3c3c3ccc;
  font-size: 18px;
  line-height: 26px;
  margin-bottom: 8px;
}

.icon_main {
  display: none;
}

.vis {
  display: block !important;
}

.value {
  line-height: 1;
  font-weight: 700;
  text-transform: uppercase;
}

.value span {
  font-weight: normal;
  text-transform: initial;
}

.value-digits {
  font-size: 29px;
  line-height: 1;
  font-family: squares, arial;
}

.value-digits span {
  font-size: 12px;
  margin-left: 3px;
}

.value-digits sup {
  margin-left: -4px;
}

.swiper-container {
  width: 100%;
  max-height: 350px;
  margin-left: auto;
  margin-right: auto;
}

.swiper-slide {
  background-size: cover;
  background-position: center;
}

.thumbs-arrows {
  position: relative;
}

.customarrowprev {
  transform: rotate(90deg);
  color: blue;
}

.customarrownext {
  transform: rotate(90deg);
  color: blue;
}

.gallery-top {
  width: 100%;
}

.gallery-thumbs {
  box-sizing: border-box;
  margin: 30px 0;
  max-height: 250px;
}

.gallery-thumbs .swiper-slide {
  opacity: 0.4;
}

.gallery-thumbs .swiper-slide-thumb-active {
  opacity: 1;
}

.arrow-prev,
.arrow-next {
  display: none !important;
}

.arrow-prev {
  top: 0;
  left: calc(50% - 44px);
  position: absolute;
  transform: scale(0.65);
  outline: none;
  cursor: pointer;
}

.arrow-next {
  bottom: 0;
  left: calc(50% - 44px);
  position: absolute;
  outline: none;
  transform: scale(0.65);
  cursor: pointer;
}

.block__text {
  text-align: center;
}

.block__button {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  flex-wrap: nowrap;
}

.block__button_icons {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: row;
  flex-wrap: nowrap;
}

.subrow {
  grid-template-columns: 1fr;
  display: grid;
  align-items: left;
}

.subrow .pricevalue {
  font-family: "squares", "arial";
  font-size: 20px;
}

.subrow .pricevalue span {
  font-size: 40px;
}

.subrow .warrianty {
  font-family: "squares", "arial";
  font-size: 40px;
  min-width: 150px;
}

.subrow .warrianty span {
  font-family: "segoeui", "arial";
  font-size: 20px;
  display: inline-block;
  margin-left: 10px;
  line-height: 1;
}

@media only screen and (min-width: 768px) {
  .description {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }

  .description {
    max-width: 95%;
    grid-gap: .5rem;
    margin: auto;
    align-items: start;
  }

  .value {
    line-height: 1.375;
  }

  .value-digits {
    font-size: 34px;
    line-height: 39px;
  }

  .block__text {
    display: flex;
    grid-column: span 3;
  }

  .block__button {
    flex-direction: row;
    grid-column: span 2;
  }

  .subrow {
    grid-template-columns: auto auto auto;
  }

  .subrow .warrianty span {
    max-width: 40px;
  }
}

@media only screen and (min-width: 1024px) {
  .cont {
    grid-template-columns: minmax(150px, 1fr) 5fr 4fr;
  }

  .swiper-pagination {
    display: none;
  }

  .description {
    max-width: 671px;
    grid-template-columns: repeat(auto-fit, minmax(6rem, auto));
    grid-auto-flow: column;
  }

  .description {
    grid-template-columns: minmax(100px, 1fr) minmax(100px, 1fr);
    grid-auto-flow: row dense;
  }

  .gallery-top {
    order: 1;
  }

  .arrow-prev,
  .arrow-next {
    display: block !important;
  }

  .block__button {
    flex-direction: row;
    justify-content: flex-start;
  }

  .subrow {
    grid-template-columns: auto auto;
  }
}

@media only screen and (min-width: 1366px) {
  .description .item_main {
    display: flex;
    flex-flow: row wrap;
  }

  .title_main {
    flex: 1 100%;
    margin-bottom: 1rem;
  }

  .icon_main {
    display: block;
    flex: 0;
    margin-right: 1rem;
    align-self: center;
  }

  .value {
    flex: 1;
  }

  .value-digits {
    flex: 1;
    font-size: 44px;
    line-height: 50px;
  }

  .block__text {
    grid-column: initial;
  }

  .block__button {
    grid-column: initial;
    flex-flow: row wrap;
    justify-content: center;
  }

  .block__button .btn__corner {
    width: 100%;
    justify-content: center;
    margin: 8px;
    max-width: 300px;
  }

  .subrow {
    grid-template-columns: 460px auto 220px;
    grid-gap: 2vw;
  }
}

@media only screen and (max-width: 1023px) {
  .description {
    text-align: center;
  }

  .thumbs-arrows {
    display: none;
  }

  .gallery-top {
    display: flex;
    flex-flow: column;
  }

  .block__text {
    justify-content: center;
    padding-bottom: 1rem;
  }
}

@media only screen and (max-width: 767px) {
  .block__button {
    margin-bottom: 30px;
  }

  .subrow .btn__corner {
    width: 100%;
    justify-content: center;
    margin: 8px 0;
    max-width: 450px;
  }
}